/* Block - loop */
.wp-block-query-pagination.site-main__loop-pagination{
    text-align: center;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;

    .wp-block-query-pagination{
        &-next,
        &-previous{
            display: inline-block;
            text-decoration: underline;
            &:hover,
            &:focus{
                text-decoration: none;
            }
        }
    }
}