/* ======================================================================== */
/* 11. Site main */
/* ======================================================================== */
.site-main{

    // Overwrite default site main if has sidebar
    &__has-sidebar{
        &.wp-block-group{
            > .wp-block-columns{
                > .wp-block-column{
                    @media screen and (max-width: 782.99px) {
                        flex-basis: 100% !important;
                        margin-left: 0;
                    }

                    &:last-child{
                        @media screen and (max-width: 782.99px) {
                            margin-top: 75px;
                        }
                    }
                }
            }
        }
    }
}