/* Block - Excerpt */
.wp-block-post-excerpt{
    &__more-link{
        display: inline-block;
        text-decoration: underline;
        text-align: center;
        min-width: 160px;
        &:hover,
        &:focus{
            color: var(--wp--preset--color--white);
            text-decoration: none;
        }
    }
}