/* Block - button */
.wp-block-button__link{
    display: inline-block;
    text-decoration: underline;
    background: var(--wp--preset--color--gray);
    color: var(--wp--preset--color--white);
    border-radius: 33px;
    padding: 19px 32px;
    font-size: var(--wp--preset--font-size--text-smallest);
    font-weight: bold;
    &:hover,
    &:focus{
        text-decoration: none;
    }
}