/* ======================================================================== */
/* 05. Custom Gutenberg wrapper */
/* ======================================================================== */
.wp-block-columns{
    &.alignfull{
        max-width: 90vw !important; // equal with theme.json

        @media screen and (max-width: 1220px) {
            max-width: 100vw !important;
            padding-left: 24px;
            padding-right: 24px;
        }
    }
    &.alignwide{
        max-width: 1170px !important;

        @media screen and (max-width: 1220px) {
            max-width: 1048px !important;
            padding-left: 24px;
            padding-right: 24px;
        }
    }
}