/* ======================================================================== */
/* 08. Spacing */
/* ======================================================================== */
.has-space{
    &-top{
        &-default{}
        
        &-smaller{
            padding-top: 40px;

            @media screen and (max-width: 1024px) {
                padding-top: 24px;
            }
        }
        &-small{
            padding-top: 100px;

            @media screen and (max-width: 1024px) {
                padding-top: 75px;
            }
        }
        &-medium{
            padding-top: 130px;

            @media screen and (max-width: 1024px) {
                padding-top: 90px;
            }
        }
        &-large{}
    }
    &-bottom{
        &-default{}

        &-smaller{
            padding-bottom: 40px;

            @media screen and (max-width: 1024px) {
                padding-bottom: 24px;
            }
        }
        &-small{
            padding-bottom: 100px;

            @media screen and (max-width: 1024px) {
                padding-bottom: 75px;
            }
        }
        &-medium{
            padding-bottom: 130px;

            @media screen and (max-width: 1024px) {
                padding-bottom: 90px;
            }
        }
        &-large{}
    }
}