/* ======================================================================== */
/* 14. Site sidebar */
/* ======================================================================== */
.site-sidebar{
    a{
        color: var(--wp--preset--color--gray-dark);
        text-decoration: underline;

        &:hover,
        &:focus{
            text-decoration: none;
            opacity: 1;
        }
    }
    // Widget
    .wp-block-latest-comments{
        padding-left: 0;
        &__meta{
            font-size: var(--wp--preset--font-size--text-default);
        }
        &__comment{
            &-author{
                
            }

            &-date{
                margin-top: 8px;
                margin-bottom: 16px;
            }
        }
    }
}