/* ======================================================================== */
/* 12. Site article */
/* ======================================================================== */
.site-article{
    &__featured-image{
        &.alignfull{
            max-width: 100% !important;
            padding-left: 0;
            padding-right: 0;

            img{
                max-height: 780px;
                object-fit: cover;
            }
        }
    }

    &__title{
        margin-bottom: 24px;
    }

    &__meta{
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: center;
        > div {
            margin-left: 6px;
            padding-left: 6px;
            margin-right: 6px;
            padding-right: 6px;
            position: relative;

            &:after{
                content: "/";
                position: absolute;
                right: -8px;
                color: var(--wp--preset--color--gray-second) !important;
            }

            &.site-article__meta{
                &-date{
                    padding-left: 0;
                    margin-left: 0;
                }
                &-tags{
                    padding-right: 0;
                    margin-right: 0;

                    &:after{
                        display: none;
                    }
                }
                &-author{
                    &:after{
                        top: -4px;
                    }
                }
            }

            a{
                text-decoration: underline;
                &:hover,
                &:focus{
                    text-decoration: none;
                }
            }
        }

        .wp-block-post-author__name{
            font-size: var(--wp--preset--font-size--text-smallest) !important;
            font-weight: normal;
            color: var(--wp--preset--color--gray-dark) !important;
        }
    }

    &__post{
        letter-spacing: 0;

        a{
            font-weight: bold;
            text-decoration: underline;
            &:hover,
            &:focus{
                text-decoration: none;
            }
        }
    }

    // Overwrite default article if has sidebar
    &__has-sidebar{
        .site-article{
            &__featured-image{
                .alignleft,
                .alignright{
                    margin-left: 0;
                    margin-right: 0;
                }
            }
            &__meta{
                justify-content: flex-start;
                padding-bottom: 24px;
                margin-bottom: 16px;
                border-bottom: 1px solid rgba(153,153,153, .2);
                
                > div{

                    &.site-article__meta-categories{
                        &:after{
                            display: none;
                        }
                    }
                }
            }
        }
    }
}